import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios'


 
const Adduser = () => {
    const navigate =useNavigate()
    const [user, setUser] = useState({

        name: "",
        username: '',
        email: '',
        phone: '',
        website: ''
    });
    

    const { name, username, phone, website, email } = user;
    const onInputChange = e => {
        setUser({ ...user, [e.target.name]: e.target.value })
    }
    const onSubmit = async (e) =>{
        e.preventDefault();
        await axios.post('http://localhost:3002/users',user);
        navigate('/')
    }

    return (
        <div className="container">
            <div className="w-75 mx-auto shadow p-5">
                <h2 className="text-center mb-4">Add User </h2>
                <from onSubmit={ e => onSubmit(e)}>
                    <div className="from-group">
                        <input style={{ marginTop: "10px", marginBottom: "10px", padding: "10px" }} className="form-control from-control-lg"
                            type="text" placeholder="Enter your name"
                            name="name" value={name} onChange={e => onInputChange(e)} />
                    </div>

                    <div className="from-group">
                        <input style={{ marginTop: "10px", marginBottom: "10px", padding: "10px" }} className="form-control from-control-lg"
                            type="text" placeholder="Enter your Username"
                            name="username" value={username} onChange={e => onInputChange(e)} />
                    </div>

                    <div className="from-group">
                        <input style={{ marginTop: "10px", marginBottom: "10px", padding: "10px" }} className="form-control from-control-lg"
                            type="text" placeholder="Enter your Email-Address" name="email"
                            value={email} onChange={e => onInputChange(e)} />
                    </div>

                    <div className="from-group">
                        <input style={{ marginTop: "10px", marginBottom: "10px", padding: "10px" }} className="form-control from-control-lg"
                            type="text" placeholder="Enter your Phone Number" name="phone"
                            value={phone} onChange={e => onInputChange(e)} />
                    </div>

                    <div className="from-group">
                        <input style={{ marginTop: "10px", marginBottom: "10px", padding: "10px" }} className="form-control from-control-lg"
                            type="text" placeholder="Enter your Website Name" name="website"
                            value={website} onChange={e => onInputChange(e)} />
                    </div>

                    <button style={{ paddingLeft: "", width: "744px" }} className="btn btn-primary btn-block"> Add User</button>

                </from>
            </div>

        </div>
    )
}

export default Adduser
