
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import Home from './componant/pages/Home'
import About from './componant/pages/About';
import Contact from './componant/pages/Contact';
import Navbar from './componant/Layout/Navbar';
import {BrowserRouter as Router,Route,Routes} from 'react-router-dom';

import Notfound from './componant/pages/Notfound';
import Adduser from './componant/Layout/Users/Adduser';
import EditUser from './componant/Layout/Users/EditUser';


function App() {
  return (
    <Router>
      <div className="App">
     <Navbar/>
     
     <Routes>
       <Route exact path="/" element={<Home />} />
       <Route exact path="/about" element={<About />} />
       <Route exact path="/contact" element={<Contact />} />
       <Route exact path ='/users/add' element={<Adduser/>} />
       <Route exact path ='/users/edit/:id' element={<EditUser/>} />
       <Route component ={<Notfound/>} />
     </Routes>
     
    </div>
    </Router>
  );
}

export default App;
