import React, { useState, useEffect } from 'react';
import axios from 'axios';
import users from "../../db.json";
import {Link, NavLink} from 'react-router-dom';


const Home = () => {
    const [user, setUser] = useState([]);

    useEffect(() => {
        
        loadUsers();

    }, [])
    const loadUsers = async () => {
        const result = await axios.get('http://localhost:3002/users');
       setUser(result.data.reverse())
        
    }
    const deleteUser =async id =>{
        await axios.delete(`http://localhost:3002/users ${id}`)
        loadUsers();
    }

   
   
    return (
        <div className="container">
            <div className="py-4">
                <h1>Home Page</h1>
                <table class="table border shadow">
                    <thead class="thead-dark">
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Name</th>
                            <th scope="col">User Name</th>
                            <th scope="col">Email</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {users.users.map((user, index) => {
                                console.log(user)
                            return <tr>
                                <th scope="row" >{index + 1}</th>
                                <td> {user.name}</td>
                                <td>{user.username}</td>
                                <td>{user.email} </td>
                                <div> 
                                    {/* <Link class="btn btn-primary mar-2">View</Link>
                                    <Link class="btn btn-outline-primary mr-2">Edit</Link>
                                    <Link class="btn btn-danger">Delete</Link> */}
                                 
                                    <Link class="btn btn-outline-primary mr-2" style={{color:'white',marginLeft:'15px',backgroundColor:"gray",borderRadius:"5px"}} to={`/users/edit/${user.id} `}>Edit</Link>
                                    <button class="btn btn-danger" style={{color:'white',marginLeft:'15px',backgroundColor:"red",borderRadius:"5px"}} onClick={()=>deleteUser(user.id)}>Delete</button>
                                    </div>
                            </tr>
                            
                        })}
                    </tbody>
                </table>


            </div>

        </div>
    )
}

export default Home;
